.gallery {
  width: calc(100% - 100px);
  height: auto;

  padding: 40px;

  box-shadow: 3px 3px 20px #dbdbdb;

  border-radius: 8px;

  margin: 50px;
}

.gallery .content {
  display: flex;
}

.image {
  width: 250px;
  height: 250px;

  background-size: cover;

  border-radius: 8px;

  margin: 5px;

  position: relative;

  transition: .5s;
}

.image-hidden {
  filter: grayscale(100%);
}

.image:hover>.image-controls {
  visibility: visible;
}

.image-controls {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 100%;

  border-radius: 8px;

  color: white;

  visibility: hidden;
}

.image-control {
  margin: 10px;
  font-size: 24px;

  cursor: pointer;
}

.file-input {
  text-align: center;
}