.manage-tips {
  width: 50%;
  height: 80vh;

  box-sizing: border-box;

  padding: 40px;

  align-items: flex-start !important;

  box-shadow: 3px 3px 20px #dbdbdb;

  border-radius: 8px;

  margin: 50px;
}

.manage-tips .tips-content {
  width: 100%;

  overflow-y: scroll;
  overflow-x: hidden;
}

.tip {
  margin: 10px;
  font-size: 16px;

  width: 100%;

  padding: 15px;
  padding-top: 25px;

  border-bottom: 1px solid lightgray;
  border-top: 1px solid lightgray;
}

.tip-content {
  text-align: center;
}

.tip .manage-buttons {
  margin-top: 20px;
}

.edit {
  color: black;
}